import axios, { all } from 'axios'
import React, { useEffect, useState } from 'react'
import LoadingPage from '../LoadingPage/LoadingPage';
import { Helmet } from 'react-helmet';

function AllOrders() {
const [allOrders, setAllOrders]=useState(null);
// const [order, setOrder]=useState(null);
    const userID = localStorage.getItem('userID')
    console.log(userID)
async function geUserOrders(){
    const res= await axios.get(`https://ecommerce.routemisr.com/api/v1/orders/user/${userID}`)
.then((res)=>{
setAllOrders(res.data);
// setOrder(res.data.cartItems);

})
.catch((err)=>{
console.log("err",err);
})


}

useEffect(()=>{
    geUserOrders(); 
},[])


return <>
<Helmet >
<title>All Orders</title>

</Helmet>

      <div className='container'>
        {allOrders ? (
          <div className='row g-2'>
            {allOrders.map((order, idx) => (
              <div key={idx} className='col-md-6  p-3  '>
                <div className='order h-100  '>
                    <div className='container'>
            <div className='row  m-2'>
{order.cartItems.map((item ,secIndex)=>{
console.log(item)
 return <div key={secIndex} className="col-md-6 mt-3">
<div className='product'>
  <img src={item.product.imageCover} alt={item.product.title} className='w-100' />
<p className='text-main'>{item.product.title.split(' ').slice(0,2).join(' ')}</p>
<p>Price : {item.price}</p>
</div>


</div>

}




)}



            </div>
                    </div>
                  <h5 className='fw-bold'>Payment Method: {order.paymentMethodType}</h5>
                  <h5 className='text-main pt-3 fw-bold'>Total Order Price: {order.totalOrderPrice}</h5>
                </div>
              </div>
            ))}
          </div>
        ) : (
          <LoadingPage />
        )}
      </div>
    </>






























}

export default AllOrders
