import { jwtDecode } from 'jwt-decode';
import React, { createContext, useEffect, useState } from 'react'

export const authContext = createContext();

function AuthContextProvider({children}) {
 const [token,setToken] = useState(null) ; 
 const [userData,setUserData] = useState(null) ; 

function getUserData(){
    const userData=   jwtDecode(localStorage.getItem('tkn') )
 console.log("userData ",userData)
   setUserData(userData)
   }

 useEffect (function(){
    const val = localStorage.getItem('tkn') 
if(val !=  null){
setToken(val);
setUserData(jwtDecode(localStorage.getItem('tkn') ))
}
 },[]) 



    return <authContext.Provider value={{myToken:token,setToken,userData,setUserData,getUserData}}>
    {children}
    
    
    </authContext.Provider>
}

export default AuthContextProvider
