import axios from 'axios';
import React, { createContext, useContext, useEffect, useState } from 'react'


export const cartContext = createContext();
function CartContextProvider({children}) {
 
// const {myToken} = useContext(authContext);
const [numOfCartItems, setNumOfCartItems] = useState(0)
const [totalCartPrice, setTotalCartPrice] = useState(0)
const [allProducts, setAllProducts] = useState(null)
const [cartID, setCartID] = useState(null)

console.log("Cart ID",cartID);

const myToken =localStorage.getItem('tkn')
  async function addProductToCart(id){
    return await axios.post('https://ecommerce.routemisr.com/api/v1/cart',{"productId":id},{
            headers:{token:localStorage.getItem('tkn')}
        }).then ((res)=>{
            getUserCart()
            return true;
        })
        .catch ((err)=>{
            getUserCart()
    
        })
        
  }

function getUserCart(){

axios.get('https://ecommerce.routemisr.com/api/v1/cart',{
    headers:{token:localStorage.getItem('tkn') }
}).then((res)=>{
    console.log("res.." ,res.data,res.data.data._id);
    setTotalCartPrice(res.data.data.totalCartPrice);
    setAllProducts(res.data.data.products);
    setNumOfCartItems(res.data.numOfCartItems);
    setCartID(res.data.data._id);
 localStorage.setItem('userID',res.data.data.cartOwner)
})
}

async function updateCount(id, newCount){
 const booleanFlag =await axios.put(`https://ecommerce.routemisr.com/api/v1/cart/${id} `,
    {'count':newCount} , { headers:{token:localStorage.getItem('tkn')}})
    .then((res)=>{
setTotalCartPrice(res.data.data.totalCartPrice);
setAllProducts(res.data.data.products)
 setNumOfCartItems(res.data.numOfCartItems)
 setCartID(res.data.data._id);

return true;
})
    .catch((err)=>{
        console.log('error',err)
        return false;

    })
    
return booleanFlag
}



async function deleteProduct(id){
const res= await axios.delete(`https://ecommerce.routemisr.com/api/v1/cart/${id}`,
 { headers:{token:localStorage.getItem('tkn')}})
 .then((res)=>{
    setTotalCartPrice(res.data.data.totalCartPrice);
    setAllProducts(res.data.data.products)
     setNumOfCartItems(res.data.numOfCartItems)
    return true;
    })
        .catch((err)=>{
            console.log('error',err)
            return false;
    
        })
}





async function clearCart(id){
    const res= await axios.delete(`https://ecommerce.routemisr.com/api/v1/cart`,
     { headers:{token:localStorage.getItem('tkn')}})
     .then((res)=>{
    setAllProducts([]);
    setNumOfCartItems(0);
    setTotalCartPrice(0);

    console.log(res)
  
            return true;

        })
            .catch((err)=>{
                console.log('error',err)
                return false;
        
            })
    }
    




  useEffect(()=>{
  console.log('getting user data');
  addProductToCart();

},[myToken]);

    return <cartContext.Provider value={{addProductToCart,numOfCartItems,totalCartPrice,allProducts,updateCount,deleteProduct,clearCart,cartID, getUserCart }}>
    
    {children}
    
    </cartContext.Provider>

}


export default CartContextProvider
