import { useFormik } from 'formik'
import React, { useState } from 'react'
import axios from 'axios';
import { ThreeDots } from 'react-loader-spinner';
import { Helmet } from 'react-helmet';
// import { useNavigate } from 'react-router-dom';



// import * as yup from 'yup';
// yup.object({
//     name:yup.string().required("Name must be required").min(3).max(25),
//    email:yup.string.email(),
//     phone: yup.string().required().matches(),
//     password: yup.string().required.min(6).max(12),
//     rePassword: yup.string().required.min(6).max(12),
// })



function Register() {
   const userData={
    name:'',
    email:'',
    phone:'',
    password: '',
    rePassword: '',

   };

 const[isSuccess, setIsSuccess]= useState(false);
 const[errMessage,setErrMessage] = useState(undefined)
 const[isLoading, setIsLoading]=useState(false);
//  const  navigate= useNavigate();

async function onSubmit(values){
    setIsLoading(true);
   
          await axios.post('https://ecommerce.routemisr.com/api/v1/auth/signup', values)
         .then(function(x){
            console.log( "in case of success:x ", x); 
          setIsSuccess(true);
          setTimeout(function () {
            setIsSuccess(false);
                    //   navigate('/login')

          },2000)
          setIsLoading(false)
         
         })   
    
 .catch(function (x) {
    console.log( "in case of error:x  ", x.response.data.message)
    setErrMessage(x.response.data.message)
    setTimeout(function () {
       setErrMessage(undefined);
      },2000)
      setIsLoading(false)
 })



   }

   

  const myFormik= useFormik({

initialValues: userData,
onSubmit : onSubmit,

validate:function(values){
    const errors ={};
    const nameRegex = /^[A-Za-z]+(?:[-' A-Za-z]+)*$/;
    const phoneRegex =/^(?:\+20|0)?1[0-9]{9}$/;
    
    if(nameRegex. test(values.name) === false){
        errors.name = "Enter your valid name"
    }

    if(values.email.includes('@') !== true && values.email.includes('.') !== true){
        errors.email = "Enter your valid Email"
    }
    if(phoneRegex.test(values.phone) === false){
        errors.phone = "Enter your valid Phone number"
    }
    if (values.password.length < 5  ){
        errors.password = "Password must be from 6 to 12 character"
    }
    if (values.rePassword != values.password  ){
        errors.rePassword = "password and re password don't match"
    }
    return errors;
    }
    
       });
       


   return <>
 <Helmet >
<title>Sign Up</title>

</Helmet>

<div className=' m-auto mt-5 w-75 '>
{isSuccess ? <div className='alert alert-success text-center'>
    <h6>Congratulations you account has been created</h6>
</div> :" "}
{errMessage ? <div className='alert alert-danger text-center'> {errMessage} </div>:" "}


<h1>Register Now :</h1>
<form action="" onSubmit={ myFormik.handleSubmit }>
<label htmlFor="name">Name</label><input onBlur={myFormik.handleBlur} onChange={myFormik.handleChange} value={myFormik.values.name} id='name' type="text" className='form-control' />
{myFormik.errors.name &&  myFormik.touched.name ?<div className='alert alert-danger mt-2'>{myFormik.errors.name} </div> : " " }

<label htmlFor="email">Email</label><input onBlur={myFormik.handleBlur} onChange={myFormik.handleChange} value={myFormik.values.email}  id='email' type="Email" className='form-control' />
{myFormik.errors.email && myFormik.touched.email ?<div className='alert alert-danger mt-2'>{myFormik.errors.email} </div> : " " }

<label htmlFor="phone">Phone</label><input  onBlur={myFormik.handleBlur} onChange={myFormik.handleChange} value={myFormik.values.phone}  id='phone' type="text" className='form-control'  />
{myFormik.errors.phone  && myFormik.touched.phone ?<div className='alert alert-danger mt-2'>{myFormik.errors.phone} </div> : " " }

<label htmlFor="password">Password</label><input  onBlur={myFormik.handleBlur} onChange={myFormik.handleChange} value={myFormik.values.password}  id='password' type="password" className='form-control' />
{myFormik.errors.password && myFormik.touched.password ?<div className='alert alert-danger mt-2'>{myFormik.errors.password} </div> : " " }

<label htmlFor="rePassword">Confirm Password</label><input onBlur={myFormik.handleBlur} onChange={myFormik.handleChange} value={myFormik.values.rePassword}  id='rePassword' type="password" className='form-control' />
{myFormik.errors.rePassword   && myFormik.touched.rePassword ?<div className='alert alert-danger mt-2'>{myFormik.errors.rePassword} </div> : " " }
<button className='btn bg-main text-white w-25 mt-3 mb-3 ' >
{ isLoading ?<ThreeDots
  visible={true}
  height="40"
  width="40"
  color="#ffff"
  radius="9"
  ariaLabel="three-dots-loading"
  wrapperStyle={{}}
  wrapperClass=""
  />:  "Submit"
}
</button>

</form>
</div>


    </>
}

export default Register
