import axios from 'axios'
import React, { useContext } from 'react'
import { FallingLines } from 'react-loader-spinner'
import { useQuery } from 'react-query'
import { Navigate, useNavigate, useParams } from 'react-router-dom'
import {cartContext} from '../../Context/CartContext'
import { toast } from 'react-hot-toast'
import Cart from './../Cart/Cart';
import LoadingPage from '../LoadingPage/LoadingPage'
import { Helmet } from 'react-helmet'


function 
ProductDetails() {
const {addProductToCart} =useContext(cartContext)

const {id} =useParams()
const  navigate= useNavigate ();


async function addProduct( id ){
const res = await addProductToCart( id );
if(res){
  
    toast.success('Added Successfully',{
        duration:1500 , 
    })
   navigate ('/Cart') }
   
else {
   
    toast.error('Error occurred ..',{
        duration:1500 ,
    })

}

}

   function getProductDetails(){
    return axios.get(`https://ecommerce.routemisr.com/api/v1/products/${ id }`)
}
   
const {isLoading , data , isError , isfatching } = useQuery(`prouctDetails-${id}`,getProductDetails)
 if(isLoading){
 return <>
  <LoadingPage />
 </>
 }
if(isError){
    return <Navigate to={'/Products'}/>
}

const productDetailsApi =data.data.data ;

 return <>
    <Helmet >
<title>{productDetailsApi.title}</title>

</Helmet>
 
 <div className='container '>

<div className='row g-3 mt-3'>
<div className='col-md-3'>
<figure>
<img className='w-100' src={productDetailsApi.imageCover} alt={productDetailsApi.description} />
</figure>
</div>
<div className='col-md-9 d-flex justify-content-center align -item-center '>
<article className='mt-5'>
<h2 className='m-auto mb-3'>{productDetailsApi.title}</h2>
<p className='m-auto'>{productDetailsApi.description}</p>
<h6 className='text-main mt-2'>{productDetailsApi.category.name}</h6>

<div className='d-flex justify-content-between mt-3 fw-bold'>
    {productDetailsApi.priceAfterDiscound ?<p> <span className='text-decoration-line-through'>{productDetailsApi.price} </span> - {productDetailsApi.priceAfterDiscound} </p> : <p>{productDetailsApi.price}</p>}

<p> <span className='m-auto'> <i style={{color:"yellowgreen"}} className='fa-solid fa-star me-2'> </i></span> 
    {productDetailsApi.ratingsAverage}</p> </div>
{/* <p>{data?.data.data.id}</p> */}
<button onClick={()=>addProduct(data?.data.data.id)}  className='btn bg-main text-white w-100'>

+ add to Cart 
</button>
</article>
</div>
</div>

 </div>



    </>
}

export default ProductDetails
