import React, { useContext } from 'react'
import { authContext } from '../../Context/AuthContext'
import LoadingPage from '../LoadingPage/LoadingPage'
import { Helmet } from 'react-helmet';

function Profile() {

    const {userData} = useContext(authContext)
    console.log(userData);
    if( !userData){
        return <LoadingPage/>
    }
    
    return <>
    <Helmet>
        <title>{userData.name}'s Profile</title>
    </Helmet>
   <div className=' container row w-75 m-auto mb-5 pb-5'>
   <div className=' col-md-2 mt-5 '>
    <h1><i  className=' userLogo ms-5 me-0 fa-solid fa-user m-auto  '></i></h1>
    </div>
    <div className='col-md-10 m-auto pt-3 mt-5 fw-bold  '>
    <h1 >Welcome  {userData.name}  </h1>
    <p className='h4 text-main'> In your profile</p>

    </div>

    </div>
    
    </>
}

export default Profile
