import React from 'react'
import {  ThreeDots } from 'react-loader-spinner'

function LoadingPage() {
    return <>
    
    
    <div className='w-100 text-center loadingScreen'>
 {/* <FallingLines 
  color="#ffff"
  width="100"
  visible={true}
  ariaLabel="falling-circles-loading"
 
   />  */}
 <ThreeDots
  visible={true}
  height="80"
  width="80"
  color="#4fa94d"
  radius="9"
  ariaLabel="three-dots-loading"
  wrapperStyle={{}}
  wrapperClass=""
  />
   </div>   
    
    
    </>
}

export default LoadingPage
