import axios from 'axios'
import React, { useContext, useEffect, useState } from 'react'
import { useQuery } from 'react-query';
import SimpleSlider from '../HomeSlider/HomeSlider';
import CategorySlider from '../CategorySlider/CategorySlider';
import { Link } from 'react-router-dom';
import { cartContext } from '../../Context/CartContext';
import { toast } from 'react-hot-toast';
import LoadingPage from '../LoadingPage/LoadingPage';
import { Helmet } from 'react-helmet';



function Products() {
    const {addProductToCart} =useContext(cartContext)
  

    
     async function addProduct( id ){
        const res = await addProductToCart( id );
        if(res){
          
            toast.success('Added Successfully',{
                duration:1500 })
            
        }
        else {
           
            toast.error('Error occurred ..',{
                duration:1500 ,
            })
        
        }
        
        }
        
        
        
 async function getAllProducts(){
return axios.get('https://ecommerce.routemisr.com/api/v1/products')
}




const {isError ,isLoading ,isFetching ,data ,isFetched} = useQuery('setAllProducts',getAllProducts,{
  cacheTime:1000
});


   return <>
     <Helmet >
<title>Products</title>

</Helmet>
   {data?.data.data ? <div className='container' >
  <div className='row m-auto mt-2 '>
    <div className='col-md-8 '>
  <SimpleSlider />
  </div>
  <div className='col-md-4'>
<img className='w-100 mt-5 ' src={require('../../Images/blog-img-1.jpeg')}alt="grocery" />
<img className='w-100 mt-5' src={require('../../Images/blog-img-2.jpeg')}alt="grocery" />
 
  </div>
  </div>
  <div className='row m-auto'>
<div className='col-md-12 '>
<CategorySlider />
</div>
    </div> 
<div className='row mt-5'>
    {data?.data.data.map((product,idx) =>
{ return <div key={idx} className='allProduct col-lg-2 col-md-3 col-sm-4 overflow-hidden '>
    
<Link to={`/ProductDetails/${product.id}`}> 
<div className='product text-center mb-2 p-2 '>
<img src={ product.imageCover} alt={product.subcategory.slug} className='w-100 '/>
<h6 className='text-main'>{product.category.name}</h6>
<p>{product.title.split(' ').slice(0,2).join(' ')}</p>
<div className='d-flex justify-content-between flex-nowrap '>
    {product.priceAfterDiscound ?<p> <span className='text-decoration-line-through'>{product.price} </span> - {product.priceAfterDiscound} </p> : <p>{product.price}</p>}

<p> <span className='m-auto'> <i style={{color:"yellowgreen"}} className='fa-solid fa-star me-2'> </i></span> 
    {product.ratingsAverage}</p>
</div>

</div> 
</Link> 

<button onClick={()=> addProduct(product.id)}  className='addbtn btn bg-main text-white m-auto d-block rounded-4 mb-2'>
+ Add to cart
</button>
</div>}

)}
</div>

</div>:  <LoadingPage/> }




</> 

}
export default Products
   
