import React, { useContext, useEffect } from 'react';
import { cartContext } from '../../Context/CartContext';
import { toast } from 'react-hot-toast';
import LoadingPage from '../LoadingPage/LoadingPage'; 
import { Link } from 'react-router-dom';
import { Helmet } from 'react-helmet';

function Cart() {
  const { clearCart, updateCount, totalCartPrice, allProducts, deleteProduct, getUserCart } = useContext(cartContext);

  if (localStorage.getItem('tkn') == null) {
    console.log("lol");
  }

  if (allProducts == null) {
    return <LoadingPage />;
  }


  async function updateMyProductCount(id, newCount) {
    const res = await updateCount(id, newCount);

    if (res) {
      toast.success('Product updated successfully');
    } else {
      toast.error('Error occurred...');
    }
  }

  async function myDeleteProduct(id) {
    const res = await deleteProduct(id);

    if (res) {
      toast.error('Error');
    } else {
      toast.success('Deleted successfully');
    }
  }

  return (
    <>
      <Helmet>
        <title>User Cart</title>
      </Helmet>
      <div className='container'>
        <h2 className='fw-bold mt-3'>Shop Cart</h2>
        <h5 className='text-main'>Total Cart Price { totalCartPrice } LE </h5>
        {allProducts.map((product, idx) => (
          <div className='row mt-2 m-auto border-1 border-bottom border-success' key={idx}>
            <div className='col-md-1  mb-2'>
              <img className='w-100' src={product.product.imageCover} alt={product.product.title} />
            </div>
            <div className='col-md-9'>
              <h5>{product.product.title} </h5>
              <h6 className='text-main'>Price :{product.price} <i onClick={() => myDeleteProduct(product.product.id)} className="fa-regular fa-trash-can text-main me-1 fs-4 ms-3 mt-1" />
 </h6>
              <p >
             
              </p>
            </div>
            <div className='col-md-2'>
              <div className='d-flex '>
                <button onClick={() => updateMyProductCount(product.product.id, product.count + 1)} className='btn btn-outline-success m-auto bg-main text-white'>
                  +
                </button>
                <p className=' m-auto p-2'>{product.count}</p>
                <button disabled={product.count === 0} onClick={() => updateMyProductCount(product.product.id, product.count - 1)} className='btn btn-outline-success m-auto bg-main text-white'>
                  -
                </button>
              </div>
            </div>
          </div>
        ))}
        {allProducts.length === 0 ? (
          <Link to={'/Products'}>
            <button className='btn btn-outline-danger m-auto mt-3'>Back To Products</button>
          </Link>
        ) : (
          <div className='mb-3'>
            <button onClick={clearCart} className='btn btn-outline-danger m-auto mt-3'>
              Clear Cart
            </button>
            <Link to={'/Payment'}>
              <button className='btn btn-outline-success m-auto mt-3 ms-3'>Confirm Payment</button>
            </Link>
          </div>
        )}
      </div>
    </>
  );
}

export default Cart;