import React from "react";
import Slider from "react-slick";
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';


export default function SimpleSlider() {
  var settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: true,      
    autoplaySpeed: 2000,
  };
  return (
    <Slider {...settings}>
      <div>
       <img className="w-100" src={require('../../Images/slider-image-1.jpeg')} alt="slider" />
      </div>
      <div>
      <img className="w-100" src={require('../../Images/slider-image-2.jpeg')} alt="slider" />
      </div> 
      <div>
      <img className="w-100" src={require('../../Images/slider-image-3.jpeg')} alt="slider" />
      </div> 
    </Slider>
  );
}