import React, { useContext } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import { authContext } from '../../Context/AuthContext'
import Login from '../Login/Login';
import { cartContext } from '../../Context/CartContext';

function Navbar() {
  const naviate =useNavigate();
  const {myToken , setToken} = useContext (authContext)
  console.log("Token in Navbar :", myToken);
  function logout() {
  setToken(null);
  localStorage.removeItem('tkn');
  naviate('/Login ');
  }

const {numOfCartItems} = useContext(cartContext)

    return <>
   <nav className="navbar navbar-expand-lg bg-body-tertiary ">
  <div className="container-fluid">
  <Link to='/Login' ><img role='buttton' src={require('../../Images/freshcart-logo.png')} alt="our logo" /> </Link> 
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon" />
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
     
      {myToken ? <div className='d-flex justify-content-between w-100'><ul className="navbar-nav  me-auto mb-2 mb-lg-0 mt-1 ">
        <li className="nav-item ">
        <Link className="navbar-brand fs-5 ms-2" to='/Products'>Home</Link>
        </li>
        <li className="nav-item ">
        <Link className="navbar-brand fs-5 "  to='/Categories'>Category</Link>
        </li>
  
        <li className="nav-item">
        <Link className="navbar-brand fs-5 "  to='/Brands'>Brands</Link>
        </li>
        <li className="nav-item">
        <Link className="navbar-brand fs-5 "  to='/AllOrders' >All Orders</Link>
        </li>
        <li className="nav-item position-relative">

        </li>
        </ul>
        <ul className="navbar-nav ms-auto mb-2 mb-lg-0 align-items-center">
             
        <Link to='/profile' > <i  className='me-3 fa-solid fa-user m-auto fs-4 '></i> </Link> 
        <Link className="navbar-brand" to='/Cart'>
          <i className='me-3 fa-solid fa-cart-shopping position-relative m-auto fs-4'>

                  <span className="position-absolute  start-120 translate-middle badge rounded-pill bg-danger">
 
 {numOfCartItems ? numOfCartItems :""}
 {/* {numOfCartItems ?? " "} */}

</span>
</i>    
</Link> 
       
              
               <li className="p-1 fs-5 " role='button' onClick={logout}>   Logout   </li>

 </ul>
              
            


      </div>  : <ul className="navbar-nav mb-2 mb-lg-0 ms-auto nav "><li className="nav-item">   
        <Link className="navbar-brand fs-5" to='/Login'>Login</Link>
        </li>   
        <li className="nav-item">   
        <Link className="navbar-brand fs-5" to='/Register'>Register</Link>
        </li> 
        </ul> }

<div>

  </div> 
    </div>
  </div>
</nav> 
    

    </>
}

export default Navbar
