import React from "react";
import Slider from "react-slick";
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import axios from "axios";
import { useQuery } from "react-query";
import LoadingPage from "../LoadingPage/LoadingPage";


export default function CategorySlider() {
  var settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 5,
    slidesToScroll: 1,
    autoplay: true,      
    autoplaySpeed: 2000,
  };

function getGategories(){
return axios.get('https://ecommerce.routemisr.com/api/v1/categories')

}


const {data, isLoading} = useQuery('CategorySlider' , getGategories)

if(isLoading){
    return <>
    
    <LoadingPage />
    
    </>
}
 return <Slider {...settings}>
    {data.data.data.map((category,idx)=>
  <div key={idx}  className="text-center container overflow-hidden">
    <img style={{height:'200px'}} className="w-100 mt-5" src={category.image} alt={category.name}/>
   <p>{category.name} </p>
    </div> )}
    
    </Slider>

}