import axios from 'axios';
import React, { useContext, useState } from 'react';
import { cartContext } from '../../Context/CartContext';
import { useFormik } from 'formik';
import { toast } from 'react-hot-toast';
import { useNavigate } from 'react-router-dom';

function Payment() {
  const  userData={
        name: '',
      details:'',
      city:'',
      phone: '',
    }    
  const { cartID ,getUserCart ,clearCart} = useContext(cartContext);
  const[isSuccess, setIsSuccess]= useState (false);
  const[errMessage,setErrMessage] = useState(undefined)
  const[isLoading, setIsLoading]=useState(false);


const navigate =useNavigate();
async function onSubmit(values){
    const res = await axios.post(
        `https://ecommerce.routemisr.com/api/v1/orders/${cartID}`, values,
        { headers: { token: localStorage.getItem('tkn') } }
      ).then((res)=>{
  if(res.data.status === 'success'){
      toast.success('Order Completed Successfully')
      clearCart();
 setTimeout(() => {
    navigate('/Products')
 }, 1500);
    }
      })
      
      .catch((err)=>{
          toast.error('Error occurred ..')
  
      })
}
 
   const myFormik = useFormik({
    initialValues:userData ,
    onSubmit : onSubmit,
    validate: function (values) {
      const errors = {};
      const textRegex = /^[0-9A-Za-z\s\.,#-]+$/i;
      const phoneRegex = /^(?:\+20|0)?1[0-9]{9}$/;

      if (textRegex.test(values.name) === false) {
        errors.name = 'Enter your valid Name';
      }
      if (textRegex.test(values.details) === false) {
        errors.address = 'Enter your valid address';
      }

 
      if (textRegex.test(values.city) === false) {
        errors.city = 'Enter your valid city';
      }
   

      if (phoneRegex.test(values.phone) === false) {
        errors.phone = 'Enter your valid phone number';
      }

      return errors;
    },
})


async function confirmOnlinePayment(values){
       const result = await axios.post (`https://ecommerce.routemisr.com/api/v1/orders/checkout-session/${cartID}`,values,
    { headers: { token: localStorage.getItem('tkn') } },{ params:"http://localhost:3000" })
    .then((result)=>{
        if(result.data.status === 'success'){
           
   
  window.open(result.data.session.url,"_self") 
          }
            })
            
 .catch((err)=>{
   toast.error('Error occurred ..',err)
        
            })
}







  return (
    <>
      <form action="" onSubmit={myFormik.handleSubmit}>
        <div className='w-50 m-auto row'>
        <div className='col-md-12'>
            <label className='mt-5 fw-bold fs-2' htmlFor='shippingAddress'>
              Shipping Details
            </label>
            <input
              className='form-control mt-3'
              type='text'
              placeholder='Enter Your Name'
              id='name'
              onBlur={myFormik.handleBlur}
              onChange={myFormik.handleChange}
              value={myFormik.values.name}
            />
            {myFormik.errors.name && myFormik.touched.name ? (
              <div className='alert alert-danger mt-2'>
                {myFormik.errors.name}{' '}
              </div>
            ) : (
              ' '
            )}
          </div>
     
          <div className='col-md-12'>
            <input
              className='form-control mt-3'
              type='text'
              placeholder='Shipping Address'
              id='details'
              onBlur={myFormik.handleBlur}
              onChange={myFormik.handleChange}
              value={myFormik.values.details}
            />
            {myFormik.errors.details && myFormik.touched.details ? (
              <div className='alert alert-danger mt-2'>
                {myFormik.errors.details}{' '}
              </div>
            ) : (
              ' '
            )}
          </div>
     
          <div className='col-md-12 mt-3'>
            <input
              className='form-control'
              type='text'
              placeholder=' City'
              id='city'
              onBlur={myFormik.handleBlur}
              onChange={myFormik.handleChange}
              value={myFormik.values.city}
            />
            {myFormik.errors.city && myFormik.touched.city ? (
              <div className='alert alert-danger mt-2'>
                {myFormik.errors.city}{' '}
              </div>
            ) : (
              ' '
            )}
          </div>
     
          <div className='col-md-12 mt-3'>
            <input
              className='form-control'
              type='text'
              placeholder=' Phone'
              id='phone'
              onBlur={myFormik.handleBlur}
              onChange={myFormik.handleChange}
              value={myFormik.values.phone}
            />
            {myFormik.errors.phone && myFormik.touched.phone ? (
              <div className='alert alert-danger mt-2'>
                {myFormik.errors.phone}{' '}
              </div>
            ) : (
              ' '
            )}
          </div>
          <div className='col-md-6 mt-3 mb-5'>
            <button type='submit' className='btn btn-success  w-100'>
              Confirm cash Payment
            </button>
            </div>
            <div className='col-md-6 mt-3 mb-5'>
          <button type='button' onClick={() => confirmOnlinePayment(myFormik.values)} className='btn btn-info m-auto  text-white  w-100'>
            Online Payment
            </button>
            </div>
         
       
        </div>
      </form>
      
    </>
  );
}


export default Payment;
