import React from 'react';
import axios from 'axios';
import { useQuery } from 'react-query';
import { Navigate } from 'react-router-dom';
import { Helmet } from 'react-helmet';
import LoadingPage from '../LoadingPage/LoadingPage';

function SubCategory({ id }) {
async function getSubCategory (id){
const response = await axios.get(`https://ecommerce.routemisr.com/api/v1/subcategories/${id}`)
    .then((response)=>{
   console.log('ressss',response.data)
        return response.data; 
    })
    .catch((error)=>{
   console.log('errorr',error) 
   

    })
};

  const { isLoading, data, isError, isFetching } = useQuery(`subCategoryDetails-${id}`, getSubCategory);

  if (isLoading) {
    return (
      <>
        <LoadingPage />
      </>
    );
  }
  return (
    <>
      <Helmet>
        <title>{data.name}</title>
      </Helmet>

      <div className="container">
        <div className="row mt-5">
          {data.map((data, idx) => ( 
              <div key={idx} className="allProduct col-lg-3 col-md-4 col-sm-2 p-2">
                <div className="h-100">
                  <img src={data.image} alt={data.slug} className="w-100 h-75" />
                  <h3 className="text-center mt-3">{data.name}</h3>
                  <p>{data._id}</p>
                </div>
              </div>
           
          ))}
        </div>
      </div>
    </>
  );
}

export default SubCategory;
