// import logo from './logo.svg';
import React from 'react';
import { RouterProvider, createBrowserRouter } from 'react-router-dom';
import './App.css';
import Login from './Components/Login/Login';
import Layout from './Components/Layout/Layout';
import Products from './Components/Products/Products';
import Register from './Components/Register/Register';
import Notfound from './Components/Notfound/Notfound';
import Categories from './Components/Categories/Categories';
import ProtectedRoute from './Components/ProtectedRoute/ProtectedRoute';
import ProductDetails from './Components/ProductDetails/ProductDetails';
import AllOrders from './Components/AllOrders/AllOrders';
import AuthContextProvider from './Context/AuthContext';
import Cart from './Components/Cart/Cart';
import {QueryClient, QueryClientProvider } from 'react-query';
import CartContextProvider from './Context/CartContext';
import { Toaster } from 'react-hot-toast';
import Payment from './Components/Payment/Payment';
import Profile from './Components/Profile/Profile'
import { Offline } from 'react-detect-offline';
import SubCategory from './Components/SubCategory/SubCategory';
import Brands from './Components/Brands/Brands';
import SubBrand from './Components/SubBrand/SubBrand';

const myRouter = createBrowserRouter([
  {path: '/' ,element: <Layout />, children:[
    
    {index:true, element:<Register />}, //Route
    {path:'/Register', element:<Register />}, //Route
    {path:'/Login', element:<Login />}, //Route
    {path:'/Products', element: <ProtectedRoute > <Products /> </ProtectedRoute> }, //Route
    {path:'*', element:<Notfound />}, //Route
    {path:'/Cart', element: <ProtectedRoute > <Cart /> </ProtectedRoute>}, //Route
    {path:'/Categories', element: <ProtectedRoute ><Categories /> </ProtectedRoute>}, //Route
    {path:'/ProductDetails/:id', element: <ProtectedRoute ><ProductDetails /> </ProtectedRoute>}, //Route
    {path:'/Payment', element: <ProtectedRoute ><Payment  /> </ProtectedRoute>}, //Route
    {path:'/SubCategory/:id', element: <ProtectedRoute ><SubCategory  /> </ProtectedRoute>}, //Route
    {path:'/AllOrders', element: <ProtectedRoute > <AllOrders  /> </ProtectedRoute>}, //Route
    {path:'/Profile', element: <ProtectedRoute > <Profile  /> </ProtectedRoute>}, //Route
    {path:'/Brands', element: <ProtectedRoute > <Brands  /> </ProtectedRoute>}, //Route
    {path:'/SubBradns/:id', element: <ProtectedRoute ><SubBrand  /> </ProtectedRoute>}, //Route

  ]}
])

function App() {

  const myClient =  new QueryClient();
  return <>
 <QueryClientProvider client={myClient}>

  <CartContextProvider>
  <AuthContextProvider>
<RouterProvider router={myRouter} />
</AuthContextProvider>
</CartContextProvider>

</QueryClientProvider>
<Toaster />
<Offline>
  <div className='p-5 bg-dark fixed-bottom text-white text-center'>
 
<h5>
Your Internet Connection has been corrupted...
</h5>
  </div>

</Offline>
</>
}

export default App;
